*
{
margin:0px;
padding:0px;
}
header
{
text-align:center;
color:white;
background-color:#4CAF50;
font-family: 'Roboto', sans-serif;
font-size: 30px;
padding:30px 0px;
}
footer
{
text-align:center;
color:white;
background-color:#4CAF50;
font:18px Roboto bold;
padding:18px 0px;
}
#ic
{
display:grid;
grid-template-columns:repeat(3,1fr);
padding:100px 200px;
}
img
{
width:250px;
height:250px;
margin:10px 5px;
border-radius:9px;
box-shadow:#999 5px 5px 5px;
}
img:hover
{
transform:scale(1.2);
}